import React, { useState } from 'react';
import { BrowserRouter as Router, Routes, Route, Navigate } from 'react-router-dom';
import AdminLogin from './components/AdminLogin';
import AdminDashboard from './components/AdminDashboard';
import AppHome from './components/AppHome';

function App() {
  const [isAdmin, setIsAdmin] = useState(false);
  const [deals, setDeals] = useState(Array(12).fill(null));

  const handleLoginSuccess = () => {
    setIsAdmin(true);
  };

  const handleUpdateDeals = (uploadedDeals) => {
    const updatedDeals = uploadedDeals.map((deal, index) => ({
      title: deal.Title || `Deal Item ${index + 1}`,
      description: deal.Description || `Exciting offer for Deal ${index + 1}!`,
      link: deal.Link || '#',
      image: deal.Image || `https://via.placeholder.com/150.png?text=Deal+${index + 1}`,
    }));
    setDeals(updatedDeals);
  };

  return (
    <Router>
      <Routes>
        {/* Home Page */}
        <Route path="/" element={<AppHome deals={deals} />} />

        {/* Admin Login */}
        <Route
          path="/genz/admin"
          element={
            isAdmin ? (
              <Navigate to="/genz/admin/dashboard" replace />
            ) : (
              <AdminLogin onLoginSuccess={handleLoginSuccess} />
            )
          }
        />

        {/* Admin Dashboard */}
        <Route
          path="/genz/admin/dashboard"
          element={
            isAdmin ? (
              <AdminDashboard onUpdateDeals={handleUpdateDeals} />
            ) : (
              <Navigate to="/admin" replace />
            )
          }
        />
      </Routes>
    </Router>
  );
}

export default App;
