import React from 'react';
import '../App.css';

const AppHome = ({ deals }) => {
  return (
    <div className="App">
      <header>
        <h1>Trending Hot Deals</h1>
      </header>

      {/* Hot Deals Section */}
      <section className="section">
        <h2>Hot Deals</h2>
        <div className="deals">
          {deals.map((deal, index) => (
            <div className="deal" key={index}>
              <img
                src={deal?.image || `https://via.placeholder.com/150.png?text=Deal+${index + 1}`}
                alt={deal?.title || `Deal ${index + 1}`}
              />
              <h3>{deal?.title || `Deal Item ${index + 1}`}</h3>
              <p>{deal?.description || `Exciting offer for Deal ${index + 1}!`}</p>
              <a href={deal?.link || '#'} target="_blank" rel="noopener noreferrer">
                <button>Shop Now</button>
              </a>
            </div>
          ))}
        </div>
      </section>
    </div>
  );
};

export default AppHome;
